"use client"

import { useState, useEffect, useMemo, useCallback } from "react"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Star, Loader2, Save, BookOpen, Calendar, TrendingUp, BookMarked, Trash2, CheckCircle2 } from "lucide-react"
import { getReadingData } from "@/lib/api-client"

export function MonthReview({ month, userEmail, monthIndex, year }: any) {
  const [allBooks, setAllBooks] = useState<any[]>([])
  const [loading, setLoading] = useState(true)
  const [bookEdits, setBookEdits] = useState<Record<string, { cover: string, rating: number, pages: number }>>({})
  const [isSaving, setIsSaving] = useState<string | null>(null)
  const [isDeleting, setIsDeleting] = useState<string | null>(null)

  const META_ANUAL = 50 // Você pode alterar este número

  const loadData = useCallback(async () => {
    if (!userEmail || !year) return
    try {
      setLoading(true)
      const data = await getReadingData(userEmail, year)
      const booksArray = Array.isArray(data) ? data : []
      setAllBooks(booksArray)

      const initialEdits: any = {}
      booksArray.forEach((b: any) => {
        initialEdits[b.book_name] = {
          cover: b.cover_url || "",
          rating: Number(b.rating) || 0,
          pages: Number(b.total_pages) || 0
        }
      })
      setBookEdits(initialEdits)
    } catch (err) {
      console.error(err)
    } finally {
      setLoading(false)
    }
  }, [userEmail, year])

  useEffect(() => {
    loadData()
  }, [loadData, monthIndex])

  const filteredBooks = useMemo(() => {
    const currentM = monthIndex + 1
    return allBooks.filter(b => Number(b.month) === currentM)
  }, [allBooks, monthIndex])

  const stats = useMemo(() => {
    const getPages = (book: any) => Number(bookEdits[book.book_name]?.pages) || Number(book.total_pages) || 0
    const finishedBooks = allBooks.filter(b => b.status === "finalizado")
    
    return {
      monthTotal: filteredBooks.length,
      monthPages: filteredBooks.reduce((acc, b) => acc + getPages(b), 0),
      yearTotal: finishedBooks.length,
      yearPages: finishedBooks.reduce((acc, b) => acc + getPages(b), 0)
    }
  }, [allBooks, filteredBooks, bookEdits])

  const handleSave = async (bookName: string) => {
    setIsSaving(bookName)
    try {
      const data = bookEdits[bookName]
      await fetch("/api/reading-data", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          action: "UPDATE_REVIEW",
          email: userEmail,
          bookName: bookName,
          rating: Number(data.rating),
          coverUrl: data.cover,
          totalPages: Number(data.pages),
          year: Number(year),
          month: monthIndex + 1
        }),
      })
      await loadData() 
    } catch (err: any) {
      alert(err.message)
    } finally {
      setIsSaving(null)
    }
  }

  const handleDelete = async (bookId: string, bookName: string) => {
    if (!confirm(`Excluir "${bookName}"?`)) return
    setIsDeleting(bookId)
    try {
      const res = await fetch(`/api/books/${bookId}`, { method: "DELETE" })
      if (res.ok) await loadData()
    } catch (err) {
      console.error(err)
    } finally {
      setIsDeleting(null)
    }
  }

  if (loading) return <div className="flex justify-center p-10"><Loader2 className="animate-spin text-primary" /></div>

  return (
    <div className="space-y-6">
      {/* Cards de Estatísticas com Barra de Progresso */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <Card className="p-4 bg-primary/5 border-primary/20 shadow-sm">
          <p className="text-[10px] font-bold uppercase text-muted-foreground flex items-center gap-2">
            <Calendar size={12}/> Resumo de {month}
          </p>
          <div className="flex justify-around mt-2">
            <div className="text-center"><span className="text-2xl font-black">{stats.monthTotal}</span><p className="text-[10px] uppercase text-muted-foreground">Lidos</p></div>
            <div className="text-center"><span className="text-2xl font-black">{stats.monthPages}</span><p className="text-[10px] uppercase text-muted-foreground">Páginas</p></div>
          </div>
        </Card>

        <Card className="p-4 bg-green-500/5 border-green-500/20 shadow-sm relative overflow-hidden">
          <p className="text-[10px] font-bold uppercase text-muted-foreground flex items-center gap-2">
            <TrendingUp size={12}/> Meta Anual {year}
          </p>
          <div className="flex justify-around mt-2">
            <div className="text-center"><span className="text-2xl font-black text-green-700">{stats.yearTotal}</span><p className="text-[10px] uppercase text-muted-foreground">Livros</p></div>
            <div className="text-center"><span className="text-2xl font-black text-green-700">{stats.yearPages}</span><p className="text-[10px] uppercase text-muted-foreground">Páginas</p></div>
          </div>
          
          {/* Barra de Progresso Visual */}
          <div className="mt-3 px-2">
            <div className="w-full bg-black/5 rounded-full h-1.5 overflow-hidden">
              <div 
                className="bg-green-600 h-full transition-all duration-700 ease-out" 
                style={{ width: `${Math.min((stats.yearTotal / META_ANUAL) * 100, 100)}%` }}
              />
            </div>
            <div className="flex justify-between mt-1">
               <p className="text-[8px] uppercase font-bold text-muted-foreground">Progresso</p>
               <p className="text-[8px] uppercase font-bold text-green-700">{Math.round((stats.yearTotal / META_ANUAL) * 100)}%</p>
            </div>
          </div>
        </Card>
      </div>

      {/* Lista de Livros */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        {filteredBooks.length === 0 ? (
          <div className="col-span-full text-center p-10 border-2 border-dashed rounded-xl opacity-50">
            <p className="text-sm">Nenhum livro finalizado em {month}.</p>
          </div>
        ) : (
          filteredBooks.map((book, idx) => (
            <Card key={idx} className="p-4 flex gap-4 hover:shadow-md transition-shadow">
              <div className="w-20 h-28 bg-muted rounded border overflow-hidden shrink-0 shadow-sm">
                {bookEdits[book.book_name]?.cover ? (
                  <img src={bookEdits[book.book_name].cover} className="w-full h-full object-cover" alt="Capa" />
                ) : (
                  <div className="w-full h-full flex items-center justify-center bg-secondary"><BookMarked className="opacity-20" size={24} /></div>
                )}
              </div>
              
              <div className="flex-1 flex flex-col gap-2 min-w-0">
                <div className="flex justify-between items-start gap-2">
                  <div className="truncate">
                    <h3 className="font-black text-xs uppercase text-primary truncate">{book.book_name}</h3>
                    {book.status === 'finalizado' && <span className="text-[8px] bg-green-100 text-green-700 px-1 rounded flex items-center gap-0.5 w-fit font-bold"><CheckCircle2 size={8}/> FINALIZADO</span>}
                  </div>
                  <button onClick={() => handleDelete(book.id, book.book_name)} className="text-muted-foreground hover:text-destructive transition-colors" disabled={isDeleting === book.id}>
                    {isDeleting === book.id ? <Loader2 size={12} className="animate-spin" /> : <Trash2 size={12} />}
                  </button>
                </div>

                <Input 
                  placeholder="URL da Capa" 
                  className="h-7 text-[10px] bg-secondary/30" 
                  value={bookEdits[book.book_name]?.cover || ""} 
                  onChange={(e) => setBookEdits(prev => ({...prev, [book.book_name]: {...prev[book.book_name], cover: e.target.value}}))} 
                />
                
                <div className="flex items-center gap-2">
                  <BookOpen size={12} className="text-primary" />
                  <Input 
                    type="number" 
                    className="h-7 w-20 text-[10px] bg-secondary/30" 
                    value={bookEdits[book.book_name]?.pages || ""} 
                    onChange={(e) => setBookEdits(prev => ({...prev, [book.book_name]: {...prev[book.book_name], pages: Number(e.target.value)}}))} 
                  />
                  <span className="text-[10px] uppercase font-bold text-muted-foreground">Páginas</span>
                </div>

                <div className="mt-auto flex justify-between items-center border-t pt-2">
                  <div className="flex gap-1">
                    {[1,2,3,4,5].map(s => (
                      <Star 
                        key={s} 
                        size={14} 
                        className={`cursor-pointer transition-colors ${s <= (bookEdits[book.book_name]?.rating || 0) ? "fill-primary text-primary" : "text-border hover:text-primary/50"}`} 
                        onClick={() => setBookEdits(prev => ({...prev, [book.book_name]: {...prev[book.book_name], rating: s}}))} 
                      />
                    ))}
                  </div>
                  <Button size="sm" className="h-7 w-7 rounded-full shadow-sm" onClick={() => handleSave(book.book_name)} disabled={isSaving === book.book_name}>
                    {isSaving === book.book_name ? <Loader2 className="animate-spin" size={12} /> : <Save size={14} />}
                  </Button>
                </div>
              </div>
            </Card>
          ))
        )}
      </div>
    </div>
  )
}