"use client"

import { useState, useEffect, useCallback } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Plus, Check, Loader2, BookOpen, Clock } from "lucide-react"
import { getReadingData } from "@/lib/api-client"

export function MonthCalendar({ month, days, year, userEmail, monthIndex }: any) {
  const [books, setBooks] = useState<any[]>([])
  const [loading, setLoading] = useState(true)
  const [newBook, setNewBook] = useState("")
  const [isAdding, setIsAdding] = useState(false)

  const loadData = useCallback(async () => {
    try {
      setLoading(true)
      const data = await getReadingData(userEmail, year)
      // Filtra apenas livros que começaram neste mês específico
      const monthBooks = data.filter((b: any) => Number(b.month) === monthIndex + 1)
      setBooks(monthBooks)
    } catch (err) {
      console.error(err)
    } finally {
      setLoading(false)
    }
  }, [userEmail, year, monthIndex])

  useEffect(() => {
    loadData()
  }, [loadData])

  const handleStartReading = async () => {
    if (!newBook) return
    setIsAdding(true)
    try {
      await fetch("/api/reading-data", {
        method: "POST",
        body: JSON.stringify({
          action: "START_READING",
          email: userEmail,
          bookName: newBook,
          startDate: new Date().toISOString(),
          year,
          month: monthIndex + 1
        })
      })
      setNewBook("")
      await loadData()
    } finally {
      setIsAdding(false)
    }
  }

  const handleFinish = async (bookName: string) => {
    try {
      await fetch("/api/reading-data", {
        method: "POST",
        body: JSON.stringify({
          action: "FINISH_READING",
          email: userEmail,
          bookName,
          endDate: new Date().toISOString(),
          year,
          month: monthIndex + 1
        })
      })
      await loadData()
    } catch (err) {
      console.error(err)
    }
  }

  return (
    <div className="space-y-6">
      {/* Barra de Adição Rápida */}
      <div className="flex gap-2">
        <Input 
          placeholder="Qual livro vamos começar hoje?" 
          value={newBook}
          onChange={(e) => setNewBook(e.target.value)}
          className="bg-background"
          onKeyDown={(e) => e.key === 'Enter' && handleStartReading()}
        />
        <Button onClick={handleStartReading} disabled={isAdding || !newBook}>
          {isAdding ? <Loader2 className="animate-spin h-4 w-4" /> : <Plus className="h-4 w-4 mr-2" />}
          Começar
        </Button>
      </div>

      {/* Grid de Dias / Status */}
      <div className="grid grid-cols-1 md:grid-cols-3 lg:grid-cols-4 gap-4">
        {books.length === 0 && !loading && (
          <div className="col-span-full py-10 text-center border-2 border-dashed rounded-xl opacity-40">
            <BookOpen className="mx-auto h-8 w-8 mb-2" />
            <p>Nenhuma leitura registrada para {month}.</p>
          </div>
        )}

        {books.map((book, idx) => (
          <Card key={idx} className={`p-4 relative group transition-all ${book.status === 'finalizado' ? 'bg-secondary/20' : 'border-primary/40 shadow-md'}`}>
            <div className="flex gap-3">
              {/* Mini Capa (se houver) */}
              <div className="w-12 h-16 bg-muted rounded border overflow-hidden shrink-0">
                {book.cover_url ? (
                  <img src={book.cover_url} className="w-full h-full object-cover" alt="capa" />
                ) : (
                  <div className="w-full h-full flex items-center justify-center bg-primary/5 text-primary/30">
                    <BookOpen size={16} />
                  </div>
                )}
              </div>

              <div className="flex-1 min-w-0">
                <h3 className="font-bold text-sm truncate uppercase tracking-tighter">{book.book_name}</h3>
                <p className="text-[10px] text-muted-foreground flex items-center gap-1 mt-1">
                  <Clock size={10} /> {new Date(book.start_date).toLocaleDateString('pt-BR')}
                </p>
                
                {book.status === 'lendo' ? (
                  <Button 
                    variant="outline" 
                    size="sm" 
                    className="h-7 w-full mt-2 text-[10px] hover:bg-green-600 hover:text-white transition-colors"
                    onClick={() => handleFinish(book.book_name)}
                  >
                    <Check className="h-3 w-3 mr-1" /> Concluir
                  </Button>
                ) : (
                  <div className="mt-2 text-[9px] font-bold text-green-600 flex items-center gap-1">
                    <Check className="h-3 w-3" /> FINALIZADO
                  </div>
                )}
              </div>
            </div>
          </Card>
        ))}
      </div>
    </div>
  )
}